*{box-sizing: border-box;font-family: -apple-system, BlinkMacSystemFont, "Apple SD Gothic Neo", "Pretendard Variable", Pretendard, Roboto, "Noto Sans KR", "Segoe UI", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif; margin: 0; padding: 0;}
html,body{
  max-width: 100%;
  background-color: #fff;
  font-size: 62.5%;
  overflow: hidden;
  @media(max-width: 1280px) {
    font-size: 58%;
  }
  @media(max-width: 900px) {
    font-size: 45%;
  }
  @media(max-width: 760px) {
    font-size: 35%;
  }
}
a {text-decoration: none;color: unset;}
@keyframes infiniteScroll {
  from {
    transform: translateX(0)
  }

  to {
    transform: translateX(-50%)
  }
}

/* include use -moz-, -webkit-, or -o- for respective browser type*/
@keyframes wheel_animation { 
  0% {
    opacity: 0;
    top: 6px;
  }
  50% {
    opacity: 1;
    top: 50%;
  }
  100% {
    opacity: 0;
    top: 27px;
  }
}

.header{
  position: fixed;
  display: block;
  z-index: 9999;
  .logoBox{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 7rem;
    height: 7rem;
    margin: 1rem;
    border: 1px solid #000;
    user-select: none;
    span{
        font-size: 1.6rem;
        font-weight: 900;
        line-height: 0.85;
        color: #000;
    }
  }
}

  
.section01{
  overflow: hidden; 
  min-width: 100vw;
  width: 100%;
  min-height: 100rem;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
	.horizontal_scrolling_items {
    position: fixed;
    top: calc( 50% - 115px );
    left: 0;
    z-index: 2;
		display: flex;
		font-size: 350px;
    font-family: "Playball", serif;
		line-height: 250px;
		font-weight: 900;
		color: rgba($color: #2a2a2a, $alpha: 0.05);
		margin: 0 auto;
		overflow: hidden;
		animation-name: infiniteScroll;
		animation-duration: 40s;
		animation-iteration-count: infinite;
		animation-timing-function: linear;
		gap: 8rem;
    .horizontal_scrolling_items_item {
      white-space: nowrap;
    }
	}
  
  .scroll_container{
    position: absolute;
    left: 50%;
    bottom: 2rem;
    transform: translateX(-50%);
    display: flex;
    flex-direction: row;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    .scroll_text{
      font-size: 2rem;
    }
    .scroll_wrap {
      border-style: solid;
      border-width: 2px;
      border-color: #000;
      border-radius: 32px;
      height: 43px;
      width: 23px;
      margin: 0 auto;
      .scrollbox_mouse {
        border-style: solid;
        border-width: 5px;
        border-color: #000;
        border-radius: 8px;
        background-color: #000;
        position: relative; 
        height: 5px;
        width: 5px;
        margin: 0 auto; 
        animation: wheel_animation 1.5s linear infinite;
      }
    }
  }
  .section_box {
    z-index: 99;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    .section_center{
      display: inline-block;
      .main_sm_text{
        display: block;
        font-size: 3.5rem;
        color: #000;
        letter-spacing: -1px;
        font-weight: 900;
        text-align: left;
      }
      .main_lg_text{
        font-size: 9rem;
        font-family: 'Termina Test', sans-serif;
        font-weight: 800;
        text-align: center;
        line-height: 1.2;
        letter-spacing: 2px;
        color: #000;
        user-select: none;
        .main_lg_span{
          border: 5px solid #000;
          margin-top: 2px;
          padding: 0 2rem;
          font-size: 9rem;
          font-family: 'Termina Test', sans-serif;
          font-weight: 900;
          line-height: 1.1;
          letter-spacing: 2px;
          color: #000;
          user-select: none;
          margin: 1rem 0;
        }
      }
    }
  }
  .spacer{
    position: fixed;
    z-index: 1;
    top: calc( 50% - 75px );
    width:100vw;
    height:180px;
    display:flex;
    justify-content:center;
    align-items:center;
    overflow:hidden;
    text-align:center;
    #scrollingText{
      position:absolute;
      color:#000;
      opacity: 0.1;
      width:2400px;
      font-size:250px;
      font-weight:900;
    }
  }
  @media(max-width: 1280px) {
    .section_box {
      .section_center{
        .main_sm_text{
          font-size: 3rem;
        }
        .main_lg_text{
          font-size: 7rem;
          .main_lg_span{
            font-size: 7rem;
          }
        }
      }
    }
    .horizontal_scrolling_items {
      top: calc( 50% - 110px );
      font-size: 300px;
      line-height: 220px;
    }
    .scroll_container{
      .scroll_wrap {
        height: 40px;
        width: 20px;
        .scrollbox_mouse {
          border-width: 4px;
          border-radius: 8px;
          height: 4px;
          width: 4px;
        }
      }
    }
  }
  @media(max-width: 900px) {
    .section_box {
      .section_center{
        .main_sm_text{
          font-size: 3.5rem;
        }
        .main_lg_text{
          font-size: 7rem;
          .main_lg_span{
            font-size: 7rem;
          }
        }
      }
    }
    .scroll_container{
      .scroll_wrap {
        height: 35px;
        width: 18px;
        .scrollbox_mouse {
          border-width: 4px;
          border-radius: 8px;
          height: 4px;
          width: 4px;
        }
      }
    }
  }
  @media(max-width: 760px) {
    .section_box {
      .section_center{
        .main_sm_text{
          font-size: 3rem;
        }
        .main_lg_text{
          font-size: 5.6rem;
          .main_lg_span{
            font-size: 5.6rem;
          }
        }
      }
    }
  }
  @media(max-width: 480px) {
    .section_box {
      .section_center{
        .main_sm_text{
          font-size: 2.4rem;
        }
        .main_lg_text{
          font-size: 4.6rem;
          .main_lg_span{
            font-size: 4.6rem;
            border: 0.5rem solid #000;
            padding: 0 1rem;
          }
        }
      }
    }
  }
}


  
.section02{
  position: relative;
  min-width: 100vw;
  padding: 0rem 0 4rem;
  width: 100%; 
  display: block;
  .box_bg{
    width: 100%;
    height: 120rem;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 1;
    background-color: #faf7f5;
  }
  .section02_list{
    font-size: 4rem;
    line-height: 1.5;
    font-weight: 500;
    color: #000;
    font-family: 'Termina Test', sans-serif;
    text-align: center;
  }
  .bg_box{
    width: 100vw;
    height: 100rem;
    position: absolute;
    // background: #eee8e5;
    background: #faf7f5;
  }
  .section_box {
    width: 100%;
    height: 100%;
    padding: 6rem 14rem 14rem;
    position: relative;
    z-index: 99;
    .title_box{
      display: flex;
      flex-direction: row;
      position: relative;
      padding-top: 4rem;
      gap: 12rem;
      .title_box_left{
        width: 500px;
        position: relative;
        .page_title{
          position: absolute;
          z-index: 3;
          text-align: right;
          top: -2rem;
          right: -8rem;
          i{
            font-family: "Playball", serif;
            display: inline-block;
            font-size: 9rem;
            color: #000;
            letter-spacing: 10px;
            font-weight: 800;
            line-height: 8rem;
          }
        }
        .page_title_img{
          width: 100%;
          margin-top: 8rem;
          display: block;
          border-radius: 10px;
          overflow: hidden;
          z-index: 9;
          img{
            display: block;
            width: 100%;
          }
        }
      }
      .title_box_right{
        padding: 8rem 0 0;
        overflow: hidden;
        span{
          display: block;
          font-size: 2rem;
          font-weight: 500;
          line-height: 1.4;
          color: #2a2a2a;
          margin-top: 2rem;
          word-break: keep-all;
        }
        .port_more{
          a{
            .link_button{
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: flex-start;
              gap: 1rem;
              margin: 2rem 0 0 0;
            }
          }
          span{
            font-size: 2rem;
            font-weight: 900;
            margin-top: 0;
          }
          img{
            width: 3.2rem;
          }
        }
      }
    }
  }
  @media(max-width: 1280px) {
    .box_bg{
      height: calc( 100% + 100px );
    }
    .section_box {
      .title_box{
        flex-direction: column;
        padding: 0;
        gap: 4rem;
        .title_box_left{
          width: 50%;
          margin: 0 auto;
          .page_title{
            right: unset;
            left: -8rem;
            text-align: left;
          }
          .page_title_img{
            border-radius: 1rem;
          }
        }
        .title_box_right{
          width: 65%;
          padding: 0;
          margin: 0 auto;
          span{
            line-height: 1.1;
          }
        }
      }
    }
  }
  @media(max-width: 760px) {
    
    .section_box {
      padding: 6rem 10rem 10rem;
      .title_box{
        .title_box_left{
          width: 60%;
          .page_title{
            left: -6rem;
          }
        }
        .title_box_right{
          width: 75%;
        }
      }
    }
  }
  @media(max-width: 640px) {
    
    .section_box {
      .title_box{
        .title_box_left{
          width: 100%;
        }
        .title_box_right{
          width: 100%;
        }
      }
    }
  }
}

.section03{
  width: 100%;
  padding: 12rem 14rem;
  position: relative;
  z-index: 99;
  .title_box{
    .page_title{
      i{
        font-family: "Playball", serif;
        display: inline-block;
        font-size: 9rem;
        color: #000;
        letter-spacing: 10px;
        font-weight: 800;
        line-height: 8rem;
      }
    }
  }
  .portfolio_box{
    margin-top: 40px;
    a{
      display: block;
      position: relative;
      .portfolio_border{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-top: 1px solid #000;
      }
      &:last-child{
        .portfolio_border{
          border-bottom: 1px solid #000;
        }
      }
      .portfolio_box_item{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        gap: 1rem;
        padding: 3rem;
        .portfolio_text_left{
          .portfolio_date{
            font-size: 2rem;
            display: block;
            width: 20rem;
            font-weight: 900;
          }
        }
        .portfolio_text_right{
          display: flex;
          flex-direction: column;
          gap: 1rem;
          .portfolio_title{
            font-size: 4rem;
            font-weight: 900;
          }
          .portfolio_skill{
            display: flex;
            flex-direction: row;
            gap: 1rem;
            flex-wrap: wrap;
            .portfolio_skill_item{
              font-size: 2rem;
              font-weight: 900;
              color: #a7a7a7;
            }
          }
        }
      }
      .cover_group{
        position: absolute;
        top: 0;
        left: 0; 
        width: 0; 
        height: 100%;
        transition: all .5s ease-in-out;
        .cover1{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: #fff;
          z-index: -1;
        }
        .cover2{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: #fff;
          z-index: 1;
          mix-blend-mode: difference;
        }
      }
      &:hover{
        .cover_group{
          width: 100%;
        }
      }
    }
  }
  @media(max-width: 1280px) {
    padding: 6rem 10rem;
    .title_box{
      .page_title{
        i{
          font-size: 8rem;
        }
      }
    }
    .portfolio_box{
      margin-top: 20px;
      a{
        .portfolio_box_item{
          padding: 2rem;
          .portfolio_text_left{
            .portfolio_date{
              font-size: 1.8rem;
              width: 18rem;
            }
          }
          .portfolio_text_right{
            .portfolio_title{
              font-size: 3rem;
            }
            .portfolio_skill{
              .portfolio_skill_item{
                font-size: 1.6rem;
              }
            }
          }
        }
      }
    }
  }
  @media(max-width: 760px) {
    .title_box{
      .page_title{
        i{
          font-size: 8rem;
        }
      }
    }
    .portfolio_box{
      a{
        .portfolio_box_item{
          padding: 2rem;
          flex-direction: column;
          align-items: flex-start;
          justify-content: flex-start;
          .portfolio_text_left{
            .portfolio_date{
              font-size: 1.8rem;
              width: 100%;
            }
          }
          .portfolio_text_right{
            .portfolio_title{
              font-size: 3rem;
            }
            .portfolio_skill{
              .portfolio_skill_item{
                font-size: 1.6rem;
              }
            }
          }
        }
      }
    }
  }
}



.section04{
  width: 100%;
  padding: 4rem 14rem;
  position: relative;
  z-index: 99;
  .box_bg{
    width: 100%;
    height: 87rem;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #faf7f5;
  }
  .title_box{
    .page_title{
      display: block;
      font-size: 3rem;
      color: #000;
      letter-spacing: -1px;
      font-weight: 900;
      z-index: 10;
      width: 100%;
      text-align: left;
    }
    .page_text{
      margin-top: 20px;
      font-size: 3rem;
      font-weight: 800;
      a{
        display: flex;
        flex-direction: row;
        gap: 1rem;
        &.number_link{
          gap: 2rem;
        }
        span{
          margin-top: 20px;
          display: block;
          font-size: 9rem;
          font-weight: 500;
          letter-spacing: -5px;
        }
      }
    }
  }
  @media(max-width: 1280px) {
    padding: 6rem 10rem 10rem;
    .title_box{
      .page_text{
        margin-top: 10px;
        font-size: 2.4rem;
        a{
          gap: 0.5rem;
          &.number_link{
            gap: 0.5rem;
          }
          span{
            margin-top: 10px;
            font-size: 7rem;
            letter-spacing: -0.7rem;
          }
        }
      }
    }
  }
}

.port_box{
  &.port_box_Lotte{
    .port_title{
      background: url('./assets/images/port_bg_2.jpg') center/cover no-repeat;
    }
  }
  &.port_box_Shinhan{
    .port_title{
      background: url('./assets/images/port_bg_3.jpg') center/cover no-repeat;
    }
  }
  &.port_box_Kustart{
    .port_title{
      background: url('./assets/images/port_bg_4.jpg') center/cover no-repeat;
    }
  }
  &.port_box_Guess{
    .port_title{
      background: url('./assets/images/port_bg_5.jpg') center/cover no-repeat;
    }
  }
  .goback_btn{
    position: fixed;
    top: 0;
    left: 12rem;
    height: 9rem;
    color: #000;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    button{
      display: flex;
      flex-direction: row;
      gap: 1rem;
      align-items: center;
      justify-content: center;
      border: none;
      background: transparent;
      font-size: 1.8rem;
      font-weight: 600;
      cursor: pointer;
      position: relative;
      padding: 0 1rem;
      &::after{
        content: " ";
        width: 0%;
        height: 2px;
        background-color: #000;
        transition: all .5s ease-in-out;
        position: absolute;
        left: 0;
        bottom: -0.4rem;
      }
      &:hover{
        &::after{
          width: 100%;
        }
      }
      img{
        width: 3rem;
      }
    }
  }
  .port_title{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    padding: 0 0 0 12rem;
    gap: 2rem;
    width: 100%;
    min-height: 100rem;
    height: 100rem;
    background: url('./assets/images/port_bg_1.jpg') center/cover no-repeat;
    overflow: hidden;
    position: relative;
    &.port_title_Lotte{
      &::after{
        content: " ";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255,255,255,0.1);
      }
    }
    &::after{
      content: " ";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.2);
    }
    .port_category{
      color: #fff;
      font-size: 6rem;
      font-weight: 600;
      position: relative;
      z-index: 99;
    }
    .port_text{
      color: #fff;
      font-size: 9rem;
      font-weight: 800;
      position: relative;
      z-index: 99;
    }
    .port_keyword{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 1rem;
      position: relative;
      z-index: 99;
      span{
        font-size: 4rem;
        font-weight: 700;
        color: #fff;
        padding-left: 1.5rem;
        position: relative;
        &:first-child{
          padding-left: 0;
          &::before{
            display: none;
          }
        }
        &::before{
          content: "/";
          position: absolute;
          left: 0;
          color: #eee;
        }
      }
    }
    .port_period{
      font-size: 4rem;
      font-weight: 600;
      color: #fff;
      position: relative;
      z-index: 99;
    }
  }
  .port_subbox{
    margin: 8rem 12rem 8rem;
    display: flex;
    flex-direction: column;
    gap: 8rem;
    .port_subbox_box{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      &.port_subbox_top{
        padding: 12rem;
        border: 1px solid #000;
        border-radius: 4rem;
        background-color: #faf7f5;
        img{
          display: block;
          max-width: 100%;
          border: 1px solid #e2e2e2;
          border-radius: 4rem;
        }
      }
      &.port_subbox_bottom{
        flex-direction: row;
        gap: 8rem;
        align-items: flex-start;
        .subbox_img{
          width: 100%;
          padding: 8rem;
          border: 1px solid #000;
          border-radius: 4rem; 
          background-color: #faf7f5;
        }
        img{
          display: block;
          max-width: 100%;
          border: 1px solid #e2e2e2;
          border-radius: 4rem;
        }
      }
    }
    .port_subbox_ment{
      font-size: 2.2rem;
      color: #2a2a2a;
      padding: 0 10rem 0;
      text-align: left;
      line-height: 1.5;
      word-break: keep-all;
    }
  }
  .port_contact{
    .title_box{
      padding: 4rem 12rem;
      background-color: #faf7f5;
      .page_text{
        margin-top: 20px;
        font-size: 3rem;
        font-weight: 800;
        text-align: center;
        a{
          display: inline-block;
          text-align: center;
          position: relative;
          margin: 0.5rem 0;
          &::after{
            content: " ";
            width: 0%;
            height: 2px;
            background-color: #000;
            transition: all .5s ease-in-out;
            position: absolute;
            left: 0;
            bottom: -0.4rem;
          }
          &:hover{
            &::after{
              width: 100%;
            }
          }
          &.number_link{
            gap: 2rem;
          }
          span{
            display: block;
            float: left;
            font-size: 9rem;
            font-weight: 500;
            padding: 0 0.2rem;
          }
        }
      }
    }
  }
  @media(max-width: 1280px) {
    .port_title{
      padding-left: 8rem;
      .port_category{
        font-size: 4rem;
      }
      .port_text{
        font-size: 7rem;
      }
      .port_keyword{
        span{
          font-size: 3.5rem;
        }
      }
      .port_period{
        font-size: 3.5rem;
      }
    }
    .port_subbox{
      margin: 7rem 10rem 7rem;
      gap: 7rem;
      .port_subbox_ment{
        padding: 0;
        font-size: 2rem;
      }
      .port_subbox_box{
        &.port_subbox_top{
          padding: 6rem;
        }
        &.port_subbox_bottom{
          gap: 7rem;
          .subbox_img{
            padding: 3rem;
          }
        }
      }
    }
    .port_contact{
      .title_box{
        padding: 4rem 0;
        background-color: #faf7f5;
        .page_text{
          font-size: 2rem;
          a{
            span{
              font-size: 7rem;
              letter-spacing: -0.7rem;
              padding: 0 0.2rem;
            }
          }
        }
      }
    }
  }
}

.footer{
  position: relative;
  z-index: 99;
  height: 5rem;
  line-height: 5rem;
  display: flex;
  flex-direction: row;
  padding: 0 2rem;
  flex-direction: row;
  justify-content: space-between;
  background-color: #faf7f5;
  span{
    font-size: 1.6rem;
    color: #000;
    font-weight: 300;
  }
}